




















import Vue from "vue";

export default Vue.extend({
  data() {
    return {
      visible: false,
      form: {
        address: "",
        amount: ""
      }
    };
  },
  methods: {
    show() {
      this.visible = true;
    },
    hide() {
      this.visible = false;
    },
    confirm() {
      if (!this.form.address) {
        return this.$message({
          message: "Please input token address.",
          type: "warning"
        });
      }

      if (!this.form.amount) {
        return this.$message({
          message: "Please input deposit amount.",
          type: "warning"
        });
      }
    }
  }
});
